.shopiLoading_screen{
    background: radial-gradient(30.69% 108.71% at 29.63% 82.87%, rgba(239, 221, 184, 0.2) 0%, rgba(252, 254, 254, 0.2) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */,
    radial-gradient(35.13% 35.18% at 73.15% 17.18%, rgba(163, 119, 235, 0.2) 0%, rgba(238, 252, 252, 0.2) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    padding: 48px 0;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.shopiLoading_screen .container {
    max-width: 1070px;
    margin: 0 auto;
}

.sl_screen_heading {
    font-size: 30px;
    font-weight: 800;
    color: #202223;
    max-width: 780px;
    margin: 0 auto;
    margin-bottom: 43px;
}

.sl_screen_heading img {
    width: 100%;
    height: auto;
    max-width: 32px;
    vertical-align: top;
    position: relative;
    top: 3px;
    margin-right: 10px;
}

.shopify-video-card-box {
    border-radius: 12px;
}

.shopify-video-card video {
    width: 100%;
    height: auto;
    border-radius: 12px 12px 0 0;
    vertical-align: top;
}

.shopify-label-card {
    font-size: 14px;
    font-weight: 600;
    color: #202223;
    text-align: left;
    padding: 18px 15px;
    border: 1px solid #E1E1E1;
    border-top: none;
    box-shadow: 0px 3px 1px 0px #1A1A1A12;
    border-radius: 0 0 12px 12px;
    background: #fff;
}

.shopiLoading_screen .spinner {
    margin: 50px auto;
    width: 40px;
    height: 40px;
}

.shopiLoading_screen .spinner:before {
    width: 40px;
    height: 40px;
    left: 50%;
    top: 50%;
    margin-top: -20px;
    margin-left: -20px;
    border: 2px solid #d1d3e0;
    border-right: 2px solid transparent;
}

@media screen and (max-width:1366px){
    .shopiLoading_screen .container {
        max-width: 850px;
    }

    .shopiLoading_screen{
        padding: 30px 0;
    }

    .sl_screen_heading{
        margin-bottom: 30px;
    }

    .sl_screen_heading {
        font-size: 28px;
    }

    .shopify-label-card {
        font-size: 13px;
    }
}